.card {
  position: relative;
  width: 290px;

  transform-style: preserve-3d;
  perspective: 1000px;

  border-radius: 4px;

  &:hover {
    cursor: pointer;
  }
}

.card__img {
  width: 290px;

  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.card__text {
  display: flex;
  justify-content: space-between;
}

.card__text--front {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 15px;

  text-transform: capitalize;
  font-size: 1rem;
  color: #102a42;

  & span {
    color: #ab7a5f;
  }
}

.card__text--back {

  font-family: "Arial", "sans-serif";
  font-size: 1rem;
  font-weight: 600;
  color: white;
  text-transform: capitalize;
  letter-spacing: .1rem;

  & span {
    font-weight: 400;
  }
}

.card__info {
  display: flex;
  flex-direction: column;

  transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
  backface-visibility: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);

  &--front {
    position: relative;
    z-index: 10;

    transform: rotateY(0deg);
  }

  &--back {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;
    row-gap: 1rem;
    justify-content: center;
    border-radius: 4px;
    background: linear-gradient(45deg, #00bbbb 0%, #6300bf 100%);
    transform: rotateY(180deg);
  }
}

.card:hover .card__info--front {
  transform: rotateY(-180deg);
}

.card:hover .card__info--back {
  transform: rotateY(0deg);
}