.card-list {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;

  @media(min-width: 768px) {
    padding: 40px;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 20px;
  }
}

.card-list__item {
  max-width: 290px;
}